<template>
	<view class="u-page">
		<view class="user-info">
			<image class="u-img" @error="onHeaderError()" :src="user.hasLogin?user.info.avatar:'/static/imgs/head.png'"></image>
			<view class="u-text">
				<view>{{user.info.nickname}}</view>
				<navigator v-if="!user.hasLogin" url="../user/login" hover-class="none"  class="small-text">
					注册/登录
				</navigator>
				<view v-else class="small-text">
					缘起缘灭，花开花谢。
				</view>
			</view>
			<view class="u-icon-setting" @click="toUserInfo">
				<u-icon name="arrow-right" color="rgb(227 227 227)" size="18"></u-icon>
			</view>
		</view>

		<view class="balance-info">
			<view class="balance-wrapper">
				<view class="balance">
					<view class="balance-money">{{user.info.money}} </view>
					<view style="font-size: 12px;">账户余额(元)</view>
				</view>
				<view class="balance-actions">
					<view class="action-recharge m-b-10">
						<u-button type="primary" icon="red-packet" size="mini">充值</u-button>
					</view>
					<view class="action-withdraw m-b-10">
						<u-button type="primary" icon="red-packet" size="mini">提现</u-button>
					</view>
				</view>
			</view>
		</view>
		
		<u-grid :col="3">
			<u-grid-item 
				@click="$utils.handleNavigate('/pages/user/rent/list')"
			>
				<u-icon name="photo" :size="46"></u-icon>
				<view class="grid-text">我的账号</view>
			</u-grid-item>
			<u-grid-item>
				<u-icon name="lock" :size="46"
					@click="$utils.handleNavigate('/pages/user/accompany/list')">
				></u-icon>
				<view class="grid-text">我的技能</view>
			</u-grid-item>
			<u-grid-item>
				<u-icon name="hourglass" :size="46"></u-icon>
				<view class="grid-text">今日订单</view>
			</u-grid-item>
		</u-grid>
		<view class="u-menu-list m-10">
			<view class="u-menu-item" @click="itemClick(item)" v-for="(item,index) in menu" :key="index">
				<u-cell-item :icon="item.icon" :title="item.name"></u-cell-item>
			</view>
		</view>
		<u-popup v-model="gameShow" mode="bottom" ref="gameRef">
			<view class="game-popup">
				<u-grid :border="false">
					<u-grid-item :col="3" v-for="(item, index) in game.data" 
						@click="$utils.handleNavigate('/pages/user/rent/release?game_id=' + item.id), $refs.gameRef.close()">
						<image class="image" :src="item.image_url" mode="widthFix"></image>
						<view>{{item.name}}</view>
					</u-grid-item>
				</u-grid>
			</view>
		</u-popup>
	</view>
</template>

<script>
	import { mapState } from 'vuex';
	export default {
		computed: {
		    ...mapState(['user', "game"]),
		}, 
		data() {
			return {
				height: 50,
				menu: [{
						name: "发布账号",
						icon: 'calendar',
						fun(that){
							// that.$utils.handleNavigate('/pages/release/account')
							that.gameShow = true
						},
					},
					{
						name: "发布技能",
						icon: 'cut',
						fun(that){
							that.$utils.handleNavigate('/pages/release/skill')
						}
					},
					{
						name: "我的收藏",
						icon: 'star',
						fun(that){
							that.$utils.handleNavigate('/pages/user/collect/collect')
						}
					},
					{
						name: "我的等级",
						icon: 'error-circle',
						fun(that){
							that.$utils.handleNavigate('/pages/user/grade/grade')
						}
					},
					{
						name: "信誉积分",
						icon: 'fingerprint',
						fun(that){
							that.$utils.handleNavigate('/pages/user/credit/credit')
						}
					},
					{
						name: "修改密码",
						path: '',
						icon: 'lock-open',
						fun(that){
							that.$utils.handleNavigate('/pages/user/modifyPwd/modifyPwd')
						},
					},
					{
						name: "退出登陆",
						path: '/pages/user/login',
						icon: 'account',
						fun(that){
							that.$store.commit("user/logout")
						}
					},
				],
				gameShow: false
			}
		},
		onLoad() {
			var _this = this;
			// 获取手机状态栏高度
			uni.getSystemInfo({
				success: function(data) {
					// 将其赋值给this
					_this.height = data.statusBarHeight;
				}
			})

			//判断是否登录

		},
		onShow() {
			this.getUserInfo();
		},
		methods: {
			getUserInfo() {
				
			},
			toUserInfo() {
				uni.navigateTo({
					url: '/pages/user/modifyPwd/modifyPwd'
				})
			},
			itemClick(item) {
				item.fun(this)
			}
		}
	}
</script>

<style lang="less" scoped>
	.u-page{
		background: #fbfbfb;
		padding-bottom: calc(var(--window-bottom));
		height: auto;
	}
	.balance-actions {
		padding: 10px 20px;
	}

	.balance-money {
		font-size: 32px;
		font-family: PingFangSC-Regular, PingFang SC;
		font-weight: 400;
		line-height: 45px;
		margin-top: 12px;
		display: inline-block;
	}

	.balance {
		padding-left: 20px;
	}

	.balance-wrapper {
		display: flex;
		justify-content: space-between;
	}

	.balance-info {
		background-color: #FFFFFF;
		margin: 10px;
		border-radius: 10px;
	}

	.user-info {
		height: 220rpx;
		display: flex;
		padding: 30rpx 40rpx 10rpx 60rpx;
		background-image: linear-gradient(135deg, #26bcff 10%, #078ef9);
		position: relative;

		.u-img {
			width: 150rpx;
			height: 150rpx;
			border-radius: 50%;
			border: 2rpx solid #FFFFFF;
		}

		.u-text {
			flex: 1;
			color: #FFFFFF;
			padding: 26rpx 30rpx;
		}

		.small-text {
			font-size: 24rpx;
			padding-top: 10rpx;
		}

		.u-icon-setting {
			width: 30rpx;
			color: #FFFFFF;
			padding-top: 50rpx;
		}
	}

	.u-menu-list {
		background: #FFFFFF;
		border-radius: 5rpx;
	}
	.game-popup{
		.image{
			width: 90%;
		}
	}
</style>